.contact{
    background-color: gray;
    padding: 20px 40px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.contactFormDiv{
    border: 2px solid black;
    width: 780px;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
}
.contactForm{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 20px;

}
.contactInput{
    height: 18px;
    width: 350px;
}
.contactTextArea{
    height: 70px;
    width: 350px;
    resize: none;
}
@media (max-width: 860px ) {
    .contactFormDiv{
        border: none;
    }
    .contactInput{
        width: 80vw;
    }
    .contactTextArea{
        width: 80vw ;
    }
}
@media (max-width: 640px) {
    .contactFormDiv{
        padding: 0
    }
    .contactInput{
        width: 98vw;
    }
    .contactTextArea{
        width: 98vw;
    }
}